import React from "react";
import style from "./howToBuy.module.css";
import Step1 from "../images/step1.jpg";
import Step2 from "../images/step2.png";
import Step3 from "../images/step3.png";
import Step4 from "../images/step4.png";

const howToBoy = (props) => {
  return <div className="container-lg center">
    <div className={style.wrapHowToBuy}>
      <h3>Здесь мы расскажем как покупать</h3>
      <span>Процесс покупки товара займет у вас всего несколько минут!</span>
      <br/>
      <br/>
      <span>Уважаемый пользователь, в этой разделе показан процесс покупки товара в несколько шагов.
        Если у вас остануться вопросы, Вы смело можете обратиться в поддержку!</span>
      <br/>

      <div className={style.styleForCount}>1</div>
      <span>Первым делом находим интересующий нас товар в каталоге. После чего кликаем по нему.</span>
      <br/>
      <img className={style.imgHowToBuy} src={Step1} />
      <br/>

      <div className={style.styleForCount}>2</div>
      <span>Вторым шагом подробно ознакамливаемся с описанием товара, выбираем количество и нажимаем кнопку "Купить"</span>
      <br/>
      <img className={style.imgHowToBuy} src={Step2} />
      <br/>

      <div className={style.styleForCount}>3</div>
      <span>Переходим к выбору способа оплаты. Внимательно выберите способ оплаты из множества перечисленных. Проверьте все заполненные поля и нажимайте "Оплатить".
      Далее в зависимости от способа оплаты откроются дополнительные страницы, где Вы укажете необходимые данные и сможете подтвердить платёж.</span>
      <br/>
      <img className={style.imgHowToBuy} src={Step3} />
      <br/>

      <div className={style.styleForCount}>4</div>
      <span>
        Приступаем к самому радостному шагу, получение товара. Если после проведения платежа вас автоматически не перебросило на получение товара,
        можно зайти напрямую через OPLATA.INFO (через вашу почту или ссылку "Способ оплаты" на сайте) или посмотреть на почте прямую ссылку для получения товара.
        Как только вы перешли к товару, копируйте его данные и активируйте их на той площадке, где они предназначены.
      </span>
      <br/>
      <img className={style.imgHowToBuy} src={Step4} />
      <br/>
      <span>
        <br/>
        <b>Как вы убедились, купить товар - это максимально просто.</b><br/>
      Ещё на несколько секунд обратим ваше внимание на последний скриншот: здесь видны контакты консультанта и сам товар.
      Доступ к этой странице вы можете получить в любой момент, кликнув по кнопке "Мои покупки" в правом верхнем углу на нашем сайте.
      </span>
      <br/>
      <br/>
    </div>
    </div>
}

export default howToBoy;